.home-main-nav {
    width: 100%;
    height: 9rem;
    display: grid;
    font-size: 17px;
    grid-template-columns: 10rem 3fr 40rem 1fr 1rem;
    backdrop-filter: blur(10px); 
    position: sticky;
    top: 0;
    left: 0;
    z-index: 1000;
    
  }
  
  .home-logo {
    display: grid;
    grid-column: 2/3;
    justify-content: start;
    align-items: center;
  }
  
  .home-logo h1 {
    font-size: 28px;
    font-weight: 600;
    text-transform: uppercase;
    color: rgb(253, 253, 253);
    text-align: left;
  }
  
  .home-logoTxt {
    margin-bottom: 5px;
    margin-left: 10px;
  }
  
  .home-menu-link {
    grid-column: 3/4;
  }
  
  .home-menu-link ul {
    height: 10rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
  
  .home-menu-link ul li {
    font-size: 1.8rem;
  }
  
  .home-menu-link ul li a {
    text-transform: capitalize;
    color: rgba(255, 255, 255, 0.8);
    transition: 0.5s;
    font-weight: 600;
  }
  
  .home-menu-link ul li:hover > a {
    font-weight: bold;
    background-color: #22bad8;
    border-radius: 10px;
    color: rgb(250, 250, 250);
    border: none;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    animation: fillBackground 0.3s forwards;
  }
  
  @keyframes fillBackground {
    0% {
      background-color: transparent;
      height: 0;
    }
    100% {
      background-color: #073842;
      height: 100%;
    }
  }
  
  @media (max-width: 520px) {
    .home-main-nav {
      height: 6rem;
      grid-template-columns: 1rem 4fr 1fr 1fr 1rem;
    }
  
    .home-logo,
    .home-social-media ul {
      height: 6rem;
    }
  
    .home-logo-image {
      width: 2rem;
      height: auto;
      margin-right: 1rem;
      margin-left: 5rem;
    }
  
    .home-logo h2 {
      font-size: 2rem;
    }
  
    .home-social-media {
      height: 6rem;
      display: flex;
      justify-self: end;
      align-items: center;
    }
  
    .home-social-media .home-hamburger-menu {
      display: block;
      font-size: 2.5rem;
    }
  
    .hero-section h1 {
      font-size: 3.8rem;
    }
  }
  
  @media (max-width: 798px) {
    .home-main-nav {
      height: 6rem;
      grid-template-columns: 1rem 2fr 1fr 1fr 1rem;
    }
  
    .home-logo,
    .home-social-media ul {
      height: 6rem;
    }
  
    .home-social-media {
      height: 6rem;
      display: flex;
      justify-self: end;
      align-items: center;
    }
  
    .home-social-media .home-hamburger-menu {
      display: block;
      font-size: 2.5rem;
    }
  }
  
  @media (max-width: 998px) {
    .home-main-nav {
      height: 7rem;
      grid-template-columns: 2rem 2fr 3fr 2rem 2rem;
    }
  
    .home-menu-link {
      display: none;
    }
  
    .home-logo,
